}

.section {
  padding-top: 60px;
  padding-bottom: 60px;
}

.section__title {
  color: var(--accent-color);
  text-align: center;
  font-size: 34px;
  font-weight: 400;
  line-height: 1.15;
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 320px) {
  .container {
    width: 320px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
  }
}

.navigation {
  width: 100%;
}

.navigation__list {
  padding-top: 123px;
}

.navigation__item {
  width: 100%;
  border-top: 1px solid #e18298;
  border-bottom: 1px solid #e18298;
  display: inline-block;
}

.navigation__link {
  color: #fff;
  transition: color 250ms var(--timing-function);
  align-items: center;
  padding: 11px 20px;
  font-family: DM Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.29;
  text-decoration: none;
  display: flex;
  position: relative;
}

.navigation__link:hover, .navigation__link:focus, .navigation__link--current {
  color: var(--accent-color);
}

.navigation__buy-btn {
  background-color: #fff;
  position: absolute;
  top: 60px;
  left: 50%;
  transform: translateX(-50%);
}

.navigation__buy-btn-icon {
  stroke: #d41443;
}

.menu__button {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin: 0;
  padding: 27px 20px;
  position: absolute;
  top: 0;
  right: 0;
}

@media screen and (min-width: 768px) {
  .menu__button {
    top: 24px;
    right: 204px;
  }
}

@media screen and (min-width: 1200px) {
  .menu__button {
    display: none;
  }
}

.mobile-menu {
  width: 100%;
  height: 100%;
  z-index: 5;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  background-color: rgba(135, 109, 109, .3);
  transition: opacity .2s linear, visibility .2s linear;
  position: fixed;
  top: 0;
  left: 0;
}

.mobile-menu.is-open {
  opacity: 1;
  visibility: visible;
  pointer-events: initial;
  transform: translateX(0);
}

.mobile-menu .content {
  width: 100%;
  height: 100vh;
  width: 250px;
  background-color: #e17992;
  transition: transform .1s linear .25s;
  display: flex;
  position: fixed;
  top: 0;
  left: calc(100% - 250px);
  transform: translateX(200%);
  box-shadow: -8px 0 20px rgba(0, 0, 0, .25);
}

.mobile-menu.is-open .content {
  transform: translateX(0);
}

.mobile-menu__button {
  z-index: 8;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  display: inline-flex;
  position: absolute;
  top: 20px;
  right: 20px;
}

.card-set {
  margin-left: calc(-1 * var(--card-gap) );
  margin-top: calc(-1 * var(--card-gap) );
  flex-wrap: wrap;
  display: flex;
}

.card-set__item {
  margin-left: var(--card-gap);
  margin-top: var(--card-gap);
  flex-basis: calc(100% / var(--card-item)  - var(--card-gap) );
}

.soc-list {
  justify-content: flex-end;
  margin-bottom: 40px;
  display: flex;
}

.soc-list__item {
  width: 32px;
  height: 32px;
  margin-right: 28px;
}

.soc-list__item:last-child {
  margin-right: 0;
}

.soc-list__link {
  width: 100%;
  height: 100%;
  cursor: pointer;
  background-color: #fea5bb;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.soc-list__link:hover, .soc-list__link:focus {
  background-color: var(--accent-color);
}

@media screen and (min-width: 768px) {
  .soc-list {
    margin-top: 60px;
  }
}

@media screen and (min-width: 1200px) {
  .soc-list {
    margin-top: 0;
  }
}

.button {
  width: 209px;
  height: 44px;
  cursor: pointer;
  color: var(--accent-color);
  transition: background-color var(--transition-function), color var(--transition-function);
  border: none;
  border-radius: 22px;
  outline: none;
  justify-content: space-around;
  align-items: baseline;
  padding: 10px 48px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.3;
  display: flex;
}

.button:hover, .button:focus {
  background-color: #ecebeb;
}

.button.current {
  background-color: var(--accent-color);
  color: var(--white-text-color);
}

.button.current:hover {
  color: var(--accent-color);
  background-color: #ecebeb;
}

.button__icon {
  stroke: var(--white-text-color);
  transition: stroke var(--transition-function);
}

button:hover .button__icon {
  stroke: var(--accent-color);
}

::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: #fff;
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(to bottom, var(--contrast-bg-color), var(--accent-color) );
  border-radius: 3px;
}

::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(to bottom, #ea2e5d, var(--accent-color) );
}

@keyframes flipInX {
  from {
    opacity: 0;
    animation-timing-function: ease-in;
    transform: perspective(400px)rotate3d(1, 0, 0, 90deg);
  }

  40% {
    animation-timing-function: ease-in;
    transform: perspective(400px)rotate3d(1, 0, 0, -20deg);
  }

  60% {
    opacity: 1;
    transform: perspective(400px)rotate3d(1, 0, 0, 10deg);
  }

  80% {
    transform: perspective(400px)rotate3d(1, 0, 0, -5deg);
  }

  to {
    transform: perspective(400px);
  }
}

@keyframes zoomIn {
  from {
    opacity: 0;
    transform: scale3d(.3, .3, .3);
  }

  50% {
    opacity: 1;
  }
}

@keyframes move-text {
  0% {
    transform: translateX(0);
  }

  100% {
    transform: translateX(10px);
  }
}

@keyframes change-color {
  0% {
    color: var(--accent-color);
  }

  100% {
    color: #f38ca5;
  }
}

@keyframes tada {
  from {
    transform: scale3d(1, 1, 1);
  }

  10%, 20% {
    transform: scale3d(.9, .9, .9)rotate3d(0, 0, 1, -3deg);
  }

  30%, 50%, 70%, 90% {
    transform: scale3d(1.1, 1.1, 1.1)rotate3d(0, 0, 1, 3deg);
  }

  40%, 60%, 80% {
    transform: scale3d(1.1, 1.1, 1.1)rotate3d(0, 0, 1, -3deg);
  }

  to {
    transform: scale3d(1, 1, 1);
  }
}

.zoomIn {
  animation-name: zoomIn;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.fadeIn {
  animation-name: fadeIn;
}

@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translate3d(0, -100%, 0);
  }

  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

@keyframes fadeInLeft {
  from {
    opacity: 0;
    transform: translate3d(-100%, 0, 0);
  }

  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

.fadeInLeft {
  animation-name: fadeInLeft;
}

@keyframes heartBeat {
  0% {
    transform: scale(1);
  }

  14% {
    transform: scale(1.3);
  }

  28% {
    transform: scale(1);
  }

  42% {
    transform: scale(1.3);
  }

  70% {
    transform: scale(1);
  }
}

@keyframes fadeInRight {
  from {
    opacity: 0;
    transform: translate3d(100%, 0, 0);
  }

  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

.fadeInRight {
  animation-name: fadeInRight;
}

@-webkit-keyframes fadeInRightBig {
  from {
    opacity: 0;
    transform: translate3d(2000px, 0, 0);
  }

  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translate3d(0, 100%, 0);
  }

  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

.fadeInUp {
  animation-name: fadeInUp;
}

@media (print), (prefers-reduced-motion: reduce) {
  .animated {
    transition-duration: 1ms !important;
    animation-duration: 1ms !important;
    animation-iteration-count: 1 !important;
  }
}

